$headerHeight: 6rem;
.container {
  position: fixed;
  height: $headerHeight;
  display: flex;
  align-items: center;
  width: 100%;
  z-index: 1;
  background-color: #000;
  border-bottom: 1px solid #333;
  transition: all 0.2s;

  &.hide {
    transform: translateY(-#{$headerHeight});
  }

  &.small {
    background-color: #fff;
    height: calc($headerHeight - 2rem);
    border-bottom: unset;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
    transition: all 0.5s;
    .logo {
      padding-left: 2rem;
      font-size: 2rem;
      color: black;
    }
  }

  .logo {
    padding-left: 2rem;
    font-size: 3rem;
    .heightLight {
      font-weight: bold;
      color: var(--color-accent);
    }
  }
}
